import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home/Home.vue'
import Introduce from '../views/Home/Introduce.vue'
import Test101 from '../views/Home/Test.vue'
import Nofound from '../views/Nofound.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/introduce',
    component: Home,
    children: [
      {
        path: 'introduce',
        component: Introduce,
        meta: {
          title: 'ChrisCN97',
          requireAuth: true
        }
      },
      {
        path: 'test',
        component: Test101
      }
    ]
  },
  {
    path: '*',
    component: Nofound
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
